import { SubjectData } from "./SubjectData";

export const Sorting = (today) => {
  let todaySubject = [];
  let todaySubjectID = [];
  let remainingSubject = [];
  SubjectData.map((item) => {
    item.dayTiming.map((subItem) => {
      if (subItem.day === today && !todaySubjectID.includes(item.id)) {
        let actualObject = { ...item, cKey: subItem.startHour };
        todaySubject.push(actualObject);
        todaySubjectID.push(item.id);
      }
    });
    if (!todaySubjectID.includes(item.id)) {
      remainingSubject.push(item);
    }
  });
  todaySubject.sort((a, b) => {
    if (a.cKey < b.cKey) {
      return -1;
    } else {
      return 1;
    }
  });
  let TodaySchedule = [...todaySubject, ...remainingSubject];
  return TodaySchedule;
};
