import { WeekDay } from "./WeekDay";

export const LectureTime = (
  hour,
  startHour,
  endHour,
  minute,
  startMinute,
  endMinute,
  subjectName,
  day
) => {
  // console.log(
  //   hour,
  //   startHour,
  //   endHour,
  //   minute,
  //   startMinute,
  //   endMinute,
  //   subjectName,
  //   day
  // );
  if (
    WeekDay[new Date().getDay()] === day &&
    hour >= startHour &&
    hour <= endHour
  ) {
    if (
      endMinute === 59 &&
      hour === startHour + 1 &&
      hour === endHour - 1 &&
      minute <= endMinute
    ) {
      return true;
    } else if (hour === startHour && minute >= startMinute) {
      return true;
    } else if (hour === endHour && minute <= endMinute && endHour !== 20) {
      return true;
    } else {
      return false;
    }
  }
  return false;
};
